@use "breakpoint" as *;

@mixin accordion-content {
  max-height: 0;
  overflow: hidden;
  transition: opacity 0.4s, max-height 0.4s;
  opacity: 0;

  &--opened {
    opacity: 1;
  }
}

@mixin accordion-plus {
  --before-transform: translate(-50%, -50%);
  --after-transform: translate(-50%, -50%) rotate(90deg);
  --after-opacity: 1;

  position: relative;

  &::before,
  &::after {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: var(--before-transform);
    content: "";
    width: 1px;
    height: 18px;
    background-color: var(--white);
    transition: transform 0.3s, background-color 0.3s;
  }

  &::after {
    transform: var(--after-transform);
    opacity: var(--after-opacity);
  }

  &.accordion-button--active {
    --before-transform: translate(-50%, -50%) rotate(90deg);
    --after-transform: translate(-50%, -50%) rotate(180deg);
    --after-opacity: 0;
  }
}

@mixin accordion-plus-span {
  --before-transform: translate(-50%, -50%);
  --after-transform: translate(-50%, -50%) rotate(90deg);

  position: relative;

  span {
    position: relative;
    display: inline-block;
    width: 15px;
    height: 15px;
    background-color: var(--bg-accent);
    border-radius: 2px;

    @include vp-768 {
      width: 30px;
      height: 30px;
    }

    @include vp-1366 {
      width: 61px;
      height: 61px;
    }

    &::before,
    &::after {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: var(--before-transform);
      content: "";
      width: 1px;
      height: 11px;
      background-color: var(--text-basic);
      transition: transform 0.3s, background-color 0.3s;

      @include vp-768 {
        height: 22px;
      }

      @include vp-1366 {
        width: 2px;
        height: 50px;
      }
    }

    &::after {
      transform: var(--after-transform);
    }
  }

  &.accordion-button--active {
    --before-transform: translate(-50%, -50%) rotate(45deg);
    --after-transform: translate(-50%, -50%) rotate(135deg);
  }
}
