:root {
  --font-family: "Inter", "Arial", sans-serif;
  --font-secondary: "Benzin", "Arial", sans-serif;
  --content-width: 1920px;
  --container-offset-mobile: 10px;
  --container-offset-tablet: 20px;
  --transparent: rgba(255, 255, 255, 0);
  --white: #ffffff;
  --black: #000000;
  --bg-light: #f2f2ef;
  --bg-dark: #2a2a2a;
  --text-extra-light: #b1b1b1;
  --text-light: #878787;
  --text-dark: #010101;
  --grey-hover: #e3e3e3;
  --grey: #969696;
  --grey-dark: #272727;
  --error: #df2312;
  --milk: #fefcf6;
}

/* stylelint-disable */
@font-face {
  font-family: "Benzin";
  src: url("../fonts/pragmatica-next_vf.woff2") format("woff2");
  font-weight: 100 900;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Light.woff2") format("woff2");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  min-width: 320px;
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.16;
  color: var(--text-basic);
  background-color: var(--bg-light);
  -webkit-tap-highlight-color: transparent;
}
@media (min-width: 1024px) {
  .page__body {
    font-size: 18px;
  }
}

.page__main {
  flex-grow: 1;
}

a {
  color: inherit;
  text-decoration: none;
}

a,
button {
  cursor: pointer;
}

a:not([href]) {
  cursor: default;
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.hidden {
  display: none;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset-mobile);
  max-width: var(--content-width);
}
@media (min-width: 768px) {
  .container {
    padding: 0 var(--container-offset-tablet);
  }
}

.centered {
  text-align: center;
}

.dis-scroll {
  overflow: hidden;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.accordion-content {
  max-height: 0;
  overflow: hidden;
  transition: opacity 0.4s, max-height 0.4s;
  opacity: 0;
}
.accordion-content--opened {
  opacity: 1;
}

.sitemap {
  padding: 50px 0;
  color: var(--black);
}

.sitemap li:not(:last-child) {
  margin: 0 0 10px;
}

.sitemap a {
  transition: opacity 0.3s;
  cursor: pointer;
}
@media (hover: hover) {
  .sitemap a:hover {
    opacity: 0.7;
    outline: none;
  }
}
.sitemap a:focus-visible {
  opacity: 0.7;
  outline: none;
}
.sitemap a:active {
  opacity: 0.5;
}
.sitemap a {
  font-size: 20px;
  color: inherit;
}

@keyframes appear {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes reveal {
  to {
    -webkit-clip-path: inset(0 0 0 0);
    clip-path: inset(0 0 0 0);
  }
}
.logo-image {
  display: block;
  width: 50px;
  height: 16px;
}
@media (min-width: 768px) {
  .logo-image {
    width: 127px;
    height: 42px;
  }
}
.logo-image[href] {
  transition: opacity 0.3s;
  cursor: pointer;
}
@media (hover: hover) {
  .logo-image[href]:hover {
    opacity: 0.7;
    outline: none;
  }
}
.logo-image[href]:focus-visible {
  opacity: 0.7;
  outline: none;
}
.logo-image[href]:active {
  opacity: 0.5;
}

.logo-image img {
  display: block;
  width: 100%;
  height: auto;
  max-height: 100%;
  object-fit: contain;
}

.title {
  margin: 0 0 10px;
  font-family: var(--font-secondary);
  font-weight: 400;
  font-size: 20px;
  line-height: 1.1;
  text-transform: uppercase;
  font-weight: 500;
  font-variation-settings: "wdth" 200;
}
@media (min-width: 1024px) {
  .title {
    font-size: 25px;
  }
}
@media (min-width: 1280px) {
  .title {
    font-size: 32px;
  }
}
@media (min-width: 1366px) {
  .title {
    font-size: 38px;
  }
}
@media (min-width: 1640px) {
  .title {
    font-size: 46px;
  }
}
@media (min-width: 1920px) {
  .title {
    font-size: 52px;
  }
}

.button {
  display: grid;
  grid-template-columns: 1fr auto;
  justify-items: start;
  align-items: center;
  gap: 10px;
}
.button::after {
  grid-area: 1/2/2/3;
  content: "";
  width: 10px;
  height: 9px;
  mask-image: url("../img/sprite.svg#arrow");
  mask-repeat: no-repeat;
  mask-size: 100% 100%;
  mask-position: center center;
  background-color: var(--white);
  transition: background-color 0.3s, transform 0.3s, opacity 0.3s;
}
.button {
  transition: color 0.3s, transform 0.3s;
}
@media (hover: hover) {
  .button:hover {
    transform: scale(1.03);
    outline: none;
  }
}
.button:focus-visible {
  transform: scale(1.03);
  outline: none;
}
.button:active {
  --color: var(--text-light);
}
.button {
  --color: var(--white);
  box-sizing: border-box;
  position: relative;
  max-width: 500px;
  padding: 8px 0;
  font-family: inherit;
  font-weight: 500;
  font-size: 12px;
  letter-spacing: -0.03em;
  line-height: 1.2;
  color: var(--color);
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  border: none;
  background-color: var(--transparent);
  cursor: pointer;
}
@media (min-width: 768px) {
  .button {
    max-width: unset;
    font-size: 14px;
  }
}
.button::before {
  position: absolute;
  bottom: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 1px;
  background-color: var(--color);
  transition: background-color 0.3s;
}
.button::after {
  background-color: var(--color);
}
.button--dark {
  --color: var(--text-dark);
}

.button-swiper {
  --color-arrow: var(--white);
  --bg-color: var(--bg-primary);
  position: relative;
  display: block;
  width: 30px;
  height: 30px;
  padding: 0;
  border: none;
  background-color: var(--bg-color);
  transition: background-color 0.3s;
  border-radius: 5px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .button-swiper {
    width: 40px;
    height: 40px;
  }
}
@media (min-width: 1366px) {
  .button-swiper {
    width: 54px;
    height: 54px;
  }
}
.button-swiper::before {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  mask-image: url("../img/sprite.svg#arrow");
  mask-position: center;
  mask-size: 100% 100%;
  mask-repeat: no-repeat;
  background-color: var(--color-arrow);
}
.button-swiper--next {
  transform: rotate(180deg);
}
@media (hover: hover) {
  .button-swiper:hover {
    --bg-color: var(--bg-accent);
    outline: none;
  }
}
.button-swiper:focus-visible {
  --bg-color: var(--bg-accent);
  outline: none;
}
.button-swiper:active {
  --bg-color: var(--bg-accent-opacity);
}
.button-swiper:disabled {
  --color-arrow: var(--grey-extra-light);
  --bg-color: var(--grey);
  cursor: default;
  pointer-events: none;
}

.button-scroll-top {
  --icon-color: var(--white);
  box-sizing: border-box;
  position: fixed;
  bottom: 30px;
  right: 8px;
  padding: 0;
  width: 39px;
  height: 39px;
  background-color: var(--bg-primary);
  border: none;
  box-shadow: 0 0 9px 4px rgba(255, 255, 255, 0.3);
  transform: translateY(-10px);
  transition: opacity 0.3s, transform 0.3s, visibility 0.3s, border 0.3s;
  visibility: hidden;
  opacity: 0;
  z-index: 100;
  cursor: pointer;
}
@media (min-width: 768px) {
  .button-scroll-top {
    right: 17px;
    width: 49px;
    height: 49px;
  }
}
@media (min-width: 1366px) {
  .button-scroll-top {
    right: 6px;
  }
}
@media (min-width: 1640px) {
  .button-scroll-top {
    right: calc(50% - var(--content-width) / 2 + 3px);
    transform: translateX(50%);
  }
}
@media (min-width: 1920px) {
  .button-scroll-top {
    right: calc(50% - var(--content-width) / 2 - 40px);
  }
}
.button-scroll-top:focus-visible, .button-scroll-top:hover {
  outline: none;
}
.button-scroll-top:active {
  opacity: 0.7;
}
.button-scroll-top::after {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  content: "";
  width: 100%;
  height: 100%;
  mask-image: url("../img/sprite.svg#arrow");
  mask-size: 20px 20px;
  mask-position: center;
  mask-repeat: no-repeat;
  background-color: var(--icon-color);
}

.button-scroll-top--visible {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}

.breadcrumbs {
  margin: 0 0 14px;
  padding: 0;
  list-style-type: none;
  font-size: 12px;
  line-height: 1.2;
}

.breadcrumbs__item {
  position: relative;
  display: inline;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.2;
}

.breadcrumbs__item:not(:last-child) {
  margin-right: 2px;
}
@media (min-width: 768px) {
  .breadcrumbs__item:not(:last-child) {
    margin-right: 3px;
  }
}
@media (min-width: 1024px) {
  .breadcrumbs__item:not(:last-child) {
    margin-right: 5px;
  }
}
.breadcrumbs__item:not(:last-child)::after {
  position: absolute;
  right: -2px;
  content: "/";
