  font-size: 14px;
  color: var(--grey);
}
@media (min-width: 768px) {
  .breadcrumbs__item:not(:last-child)::after {
    right: -3px;
    font-size: 18px;
  }
}
@media (min-width: 768px) {
  .breadcrumbs__item:not(:last-child)::after {
    right: -4px;
    font-size: 20px;
  }
}

.breadcrumbs__link {
  display: inline;
  font-weight: 400;
  font-size: 14px;
  letter-spacing: -0.01em;
  line-height: 1.2;
  color: var(--grey);
}
@media (min-width: 768px) {
  .breadcrumbs__link {
    font-size: 18px;
  }
}
@media (min-width: 1024px) {
  .breadcrumbs__link {
    font-size: 20px;
  }
}

span.breadcrumbs__link {
  color: var(--text-basic);
}

.map__wrapper {
  display: grid;
  height: 300px;
  border-radius: 5px;
  overflow: hidden;
}
@media (min-width: 768px) {
  .map__wrapper {
    height: 520px;
  }
}
@media (min-width: 1366px) {
  .map__wrapper {
    height: 741px;
  }
}

.map__image {
  grid-area: 1/1/2/2;
  margin: auto;
  width: 100%;
  height: 100%;
  background-color: var(--grey);
}

.map__image img {
  display: block;
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.map__iframe {
  grid-area: 1/1/2/2;
}

.map__iframe iframe {
  height: 100%;
}

.modal {
  position: fixed;
  box-sizing: border-box;
  overflow-y: auto;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  padding: 20px 0;
  background-color: rgba(255, 255, 255, 0.8);
  visibility: hidden;
  opacity: 0;
  z-index: 300;
}
.modal.open {
  visibility: visible;
  opacity: 1;
  transition: visibility 0.4s, opacity 0.4s;
}

.modal__container {
  position: relative;
  box-sizing: border-box;
  width: calc(100% - 20px);
  max-width: 500px;
  padding: 20px;
  margin: auto;
  color: var(--white);
  background-color: var(--bg-accent);
  border-radius: 5px;
}
@media (min-width: 768px) {
  .modal__container {
    max-width: 680px;
    padding: 30px;
  }
}

.modal--photo .modal__container {
  padding: 0;
}
@media (min-width: 768px) {
  .modal--photo .modal__container {
    max-width: calc(100% - 200px);
    padding: 0;
  }
}
@media (min-width: 1024px) {
  .modal--photo .modal__container {
    max-width: calc(100% - 300px);
    padding: 0;
  }
}
@media (min-width: 1640px) {
  .modal--photo .modal__container {
    max-width: 1280px;
  }
}

.modal__close-button {
  transition: opacity 0.3s;
  cursor: pointer;
}
@media (hover: hover) {
  .modal__close-button:hover {
    opacity: 0.7;
    outline: none;
  }
}
.modal__close-button:focus-visible {
  opacity: 0.7;
  outline: none;
}
.modal__close-button:active {
  opacity: 0.5;
}
.modal__close-button {
  position: relative;
  width: 20px;
  height: 20px;
  padding: 0;
  background-color: var(--transparent);
  border: none;
  z-index: 1;
  cursor: pointer;
}
.modal__close-button::before, .modal__close-button::after {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  content: "";
  width: 18px;
  height: 2px;
  background-color: var(--white);
  z-index: 5;
}
.modal__close-button::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}
.modal__close-button {
  position: absolute;
  top: 0;
  right: 0;
  width: 30px;
  height: 30px;
}
@media (min-width: 1024px) {
  .modal__close-button {
    width: 40px;
    height: 40px;
  }
}

.modal__image {
  width: 100%;
  max-width: 1280px;
  max-height: 720px;
  aspect-ratio: 1280/720;
  margin: 0 auto;
  opacity: 0;
  transform: scale(0.6);
  transition: transform 0.6s 0.1s, opacity 0.6s 0.1s;
}
.modal__image img {
  display: block;
  object-fit: cover;
  object-position: center;
  height: 100%;
  width: 100%;
  background-color: var(--text-light);
}

.modal.open .modal__image {
  opacity: 1;
  transform: scale(1);
}

[data-animation] {
  opacity: 0;
  transition: opacity 0.6s ease-out, transform 0.6s ease-out;
}

[data-animation].animated {
  opacity: 1;
  transform: none;
}

[data-animation=fade-in] {
  transform: none;
}

[data-animation=slide-up] {
  transform: translateY(50px);
}

[data-animation=slide-down] {
  transform: translateY(-50px);
}

[data-animation=slide-left] {
  transform: translateX(-50px);
}

[data-animation=slide-right] {
  transform: translateX(50px);
}

[data-animation=zoom] {
  transform: scale(0.8);
}

[data-animation=rotate] {
  transform: rotate(-10deg) scale(0.9);
}

.header {
